<template>
  <div class="votemanage">
    <p class="votepage-head">
      <a-icon @click="fToRoutePre" type="left" class="head-left"></a-icon>
      <span>翼企投</span>
    </p>
    <div v-if="aVoteList.length == 0">
      <img class="novote-img" src="../assets/novote.png">
      <p class="novote-descript">暂无投票</p>
    </div>
    <div @click="fToVoteDetail(item)" v-for="(item,key) in aVoteList" :key="key" class="votepage-contentp">
      <span class="contentp-left">{{item.title}}</span>
      <span class="contentp-right">{{item.totalvote}}票</span>
    </div>
    <p class="votepage-myvote">
      <a-icon type="appstore" class="head-right"></a-icon>
      <span>我的投票</span>
    </p>
  </div>
</template>

<script>
export default {
  name: 'VoteManage',
  data () {
    return {
      // 用户参与的投票列表
      aVoteList: []
    }
  },
  created () {
    this.fGetData()
  },
  watch: {
    $route (to, from) {
      if (this.$route.params.voted === true) {
        this.fGetData()
      } else {
        console.log(this.$route.params)
      }
    }
  },
  methods: {
    fGetData () {
      this.aVoteList = []
      this.$axios.get('/list')
        .then((result) => {
          console.log(result.data)
          result.data.forEach((item, index) => {
            let singlevote = {title: '', totalvote: 0, voteId: 0}
            singlevote.title = item.title
            singlevote.voteId = item.id
            singlevote.totalvote = item.totalNum
            this.aVoteList.push(singlevote)
          })
        })
    },
    fToRoutePre () {
      this.$router.back(-1)
    },
    fToVoteDetail (val) {
      this.$router.push({name: 'VoteHome', params: {voteId: val.voteId, voted: false}})
    }
  }
}
</script>

<style scoped>
.votemanage{
  width: 100%; height: 100vh;
  overflow: scroll; text-align: left;
  background-color: #f0f2f5;
}
.votemanage::-webkit-scrollbar{ display: none;}
/* 头部 */
.votepage-head{
  height: 44px; line-height: 44px; padding: 0 4% 0 3.7%; margin-bottom: 1px;
  text-align: center; font-size: 1.5rem; font-weight: bold;
  background-color: #fff;
}
.votepage-head .anticon{ font-size: 1.25rem;}
.head-left{float: left; margin-top: 11px; color: #108EE9;}
/* 内容 */
.novote-img{
  width: 54.4%;
  margin: 19% 0 20px 22.9%;
  max-width: 550px;
}
.novote-descript{
  text-align: center;
  font-size: 1.125rem;
}

.votepage-contentp{
  padding: 11px 4%; margin: 1px 0;
  background-color: #fff;
  font-size: 1rem; line-height: 24px; color: #000;
}
.contentp-left{
  display: inline-block; width: 85%;
  overflow: hidden; white-space: nowrap; text-overflow: ellipsis;
}
.contentp-right{ float: right;}
/* 尾部 */
.votepage-myvote{
  position:absolute; bottom: 0;
  width: 100%; height: 50px; padding: 17px; margin: 0;
  font-size: 0.875rem; text-align:center;
  background-color: #fff; color: #108EE9;
}
.votepage-myvote .anticon-appstore{ width: 14px;}
.votepage-myvote span{ padding-left: 2.9%;}
</style>



