import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import VoteHome from '@/components/VoteHome'
import VoteManage from '@/components/VoteManage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/votehome',
      name: 'VoteHome',
      component: VoteHome
    },
    {
      path: '/votemanage',
      name: 'VoteManage',
      component: VoteManage
    }
  ]
})



