<template>
<div class="votepage">
  <!--页面头部-->
  <p class="votepage-head">
    <a-icon type="left" class="head-left"></a-icon>
    <span>翼企投</span>
    <a-icon @click="fRouterToManage" type="appstore" class="head-right"></a-icon>
  </p>
  <!--投票标题-->
  <div class="votepage-contentp votepage-contenttitle">
    <p class="votepage-title">
      <span>投票标题:</span>
      <span>{{sVoteTitle}}</span>
      <!-- <img v-if="sLimit !== '限制'" alt="分享" @click="fVoteShare" class="title-right" src="../assets/share.png"> -->
    </p>
    <p class="votepage-type">({{sVoteType}})</p>
    <p class="votepage-descript">{{sVoteDescript}}</p>
  </div>
  <!--选项-->
  <div>
    <div v-for="(item, index) in oVoteChoice.aVoteChoice" :key="index">
      <!--选项基本信息\票数-->
      <p @click="fChoiceWhich(index)" class="votepage-contentp" :class="{'ischoose': nRadioNow == index || oMultipleNow[index] == true, 'votepage-img':oVoteChoice.nOption == 1 }">
        <span v-if="oVoteChoice.nOption == 1">
          <img class="choice-img" v-lazy="item.name" alt="photo">
          <span class="choice-content">{{item.value}}</span>
        </span>
        <span v-if="oVoteChoice.nOption == 0">
          <span>选项:</span>
          <span class="votepage-editcontent">{{item.value}}</span>
        </span>
        <span v-if="bIsVoted" class="votepage-votenum">
            <span>{{item.votenum}}票</span>
            <span v-if="item.votenum !== 0">{{parseInt(((item.votenum/sVoteTotal)*100))}}%</span>
            <span v-if="item.votenum == 0">0%</span>
        </span>
        <a-progress v-show="bIsVoted" strokeLinecap="square" style="height: 2px;"
                    :showInfo="false" :percent="item.votenum == 0? 0 : parseInt(((item.votenum/sVoteTotal)*100))">
        </a-progress>
      </p>
      <!--实名展示-->
      <p class="votepage-votewho" v-if="bIsVoted && sVoteWay == '实名'">
        <span>
          <a-avatar class="votewho-single" v-for="(item1,key) in votepeople" :key = "key"
                    v-if="key < 3 && key < item.votenum && item.votenum !== 0" size="small" :src="item1.avatar">
          </a-avatar>
        </span>
        <a-avatar v-if="item.votenum > 0"
                   icon="ellipsis" style="backgroundColor: #e9e9e9; color: #888;"
                   size="small"  @click="fShowMoreChoicer(index)">
        </a-avatar>
      </p>
      <!--显示更多名字-->
      <a-modal class="choice-modal" v-if="now == index" v-model="visiable" :closable="false">
        <a-row v-for="(item1,key) in votepeople" :key = "key" v-if="key < item.votenum">
          <a-col :span="4">
            <a-avatar :src="item1.avatar"></a-avatar>
          </a-col>
          <a-col :span="8" :offset="1">
            <span class="votewho-name">{{item1.name}}</span>
          </a-col>
        </a-row>
        <template slot="footer">
          <p class="votewho-more-footer" @click="handleOk">知道了</p>
        </template>
      </a-modal>
    </div>
  </div>
  <!--时间\按钮-->
  <p class="votepage-time">
    <span>投票截止日期:</span>
    <span>{{sVoteTime}}</span>
  </p>
  <p v-if="!bIsVoted" class="votepage-tovote">
    <a-button @click="fVoteSubmit" class="tovote-bt" type="primary" block>投票</a-button>
  </p>
  <a-modal class="votesuccess-modal" :closable="false" v-model="visiableVoted">
    <h3>温馨提示</h3>
    <p>恭喜您投票成功!</p>
    <template slot="footer">
      <p class="votewho-more-footer" @click="fVotedOk">知道了</p>
    </template>
  </a-modal>
</div>
</template>

<script>
import imgurl from '../assets/product.png'
var qs = require('qs')
export default {
  name: 'VoteHome',
  data () {
    return {
      // 模拟使用：存放总data
      axiosData: '',
      // 模态框提醒已投票\显示更多用户
      visiableVoted: false,
      visiable: false,
      now: 10000,
      // 选择添加样式参数
      nRadioNow: 1000,
      oMultipleNow: [],
      // 保存当前选择
      sRadioChoice: '',
      aMultipleChoice: [],
      // 数据
      // 模拟实名
      votepeople: [
        {name: '人员1', avatar: imgurl},
        {name: '人员2', avatar: imgurl},
        {name: '人员3', avatar: imgurl},
        {name: '人员4', avatar: imgurl},
        {name: '人员5', avatar: imgurl},
        {name: '人员6', avatar: imgurl},
        {name: '人员7', avatar: imgurl},
        {name: '人员8', avatar: imgurl},
        {name: '人员9', avatar: imgurl},
        {name: '人员10', avatar: imgurl}
      ],
      nVoteId: 0,
      sVoteTitle: '模拟投票1',
      sVoteType: '多选',
      sVoteWay: '实名',
      sVoteDescript: '详细说明详细说明详细说明详细说明详细说明详细说明详细说明',
      oVoteChoice: {
        nOption: 1,
        // aVoteChoice: [
        //   { name: imgurl,
        //     value: '选项1',
        //     votenum: 68,
        //     votewho: [
        //       {name: '人员1', avatar: imgurl},
        //       {name: '人员2', avatar: imgurl},
        //       {name: '人员3', avatar: imgurl},
        //       {name: '人员4', avatar: imgurl},
        //       {name: '人员5', avatar: imgurl},
        //       {name: '人员6', avatar: imgurl},
        //       {name: '人员7', avatar: imgurl},
        //       {name: '人员8', avatar: imgurl},
        //       {name: '人员9', avatar: imgurl}
        //     ]},
        //   { name: imgurl,
        //     value: '2',
        //     votenum: 32,
        //     votewho: [
        //       {name: '人员1', avatar: imgurl},
        //       {name: '人员2', avatar: imgurl},
        //       {name: '人员2', avatar: imgurl}
        //     ]},
        //   { name: 3,
        //     value: '3',
        //     votenum: 0,
        //     votewho: []},
        // ],
        aVoteChoice: []
      },
      sVoteTime: '2019-05',
      sLimit: '不限制',
      sVoteTotal: 0,
      /* *************该数据可能为判断用户投票列表是否包含此投票获取******* */
      bIsVoted: false
    }
  },
  created () {
    // 若不是由点击链接进入,渲染前需先判断是否已经投过票
    // if (Object.keys(this.$route.params).length === 0) {
    //   console.log(1)
    //   return false
    // }
    this.getdata()
  },
  updated () {
    this.initProgressStyle()
  },
  watch: {
    // 由个人投票列表跳转,根据voteId查找
    $route (to, from) {
      if (from.name === 'VoteManage') {
        if (this.$route.params !== {}) {
          // 清空选项
          this.nRadioNow = 1000
          this.oMultipleNow = []
          this.axiosData.forEach((item, key) => {
            if (item.id === this.$route.params.voteId) {
              this.nVoteId = this.$route.params.voteId
              let data = item
              this.fChangeData(data)
              this.bIsVoted = this.$route.params.voted
              return false
            }
          })
        } else return false
      }
    }
  },
  methods: {
    getdata () {
      this.$axios.get('/list').then((result) => {
        console.log(result.data)
        this.axiosData = result.data
        this.axiosData.forEach((item, index) => {
          if (item.id === this.nVoteId) {
            let data = item
            this.fChangeData(data)
            return false
          }
        })
      })
    },
    // 转换数据渲染
    fChangeData (data) {
      this.sVoteTitle = data.title
      this.sVoteType = data.type === 0 ? '单选' : '多选'
      this.sVoteDescript = data.detail
      this.sVoteWay = data.anonymity === 0 ? '匿名' : '实名'
      this.oVoteChoice.nOption = data.optionsType
      // eslint-disable-next-line quotes
      this.oVoteChoice.aVoteChoice = JSON.parse(data.options)
      // console.log(this.oVoteChoice.aVoteChoice)
      this.sVoteTime = data.deadline
      this.sVoteTotal = data.totalNum
      console.log('票数' + this.sVoteTotal)
      this.oVoteChoice.aVoteChoice.forEach((item, index) => {
        // this.oMultipleNow[index] = false
        this.oMultipleNow.push(false)
      })
      // console.log(this.oMultipleNow)
    },
    /* ????????????????初始化进度条内联样式?????????????????????? */
    initProgressStyle () {
      document.querySelectorAll('.ant-progress-bg').forEach((item, index) => {
        item.style.height = '2px'
      })
    },
    // 跳转至管理页
    fRouterToManage () {
      this.$router.push({name: 'VoteManage', params: {voted: this.bIsVoted}})
    },
    // 分享投票
    fVoteShare () {
      console.log('分享投票')
    },
    // 判断是否投过票,点击添加样式
    fChoiceWhich (val) {
      // 若已投票,则无法触发点击事件
      if (this.bIsVoted === true) {
        event.preventDefault()
        return
      }
      // 判断单选多选绑定类名
      if (this.sVoteType === '单选') {
        this.nRadioNow = val
      } else {
        this.oMultipleNow[val] = !this.oMultipleNow[val]
        this.oMultipleNow.push(true)
        this.oMultipleNow.pop()
        console.log(this.oMultipleNow)
      }
    },
    // 提交保存当前选项,判断不能为空
    fVoteSubmit () {
      let updateOptions = {options: [], totalNum: 0, voteId: 0}
      let addnum = this.sVoteTotal
      // 单选
      if (this.sVoteType === '单选') {
        this.sRadioChoice = this.nRadioNow
        if (this.sRadioChoice === 1000) {
          this.$message.warning('选择不能为空')
          return
        }
        // 对应选项加1后保存
        let num = this.oVoteChoice.aVoteChoice[this.sRadioChoice].votenum
        num++
        addnum++
        this.oVoteChoice.aVoteChoice[this.sRadioChoice].votenum = num
        updateOptions.options = JSON.stringify(this.oVoteChoice.aVoteChoice)
        updateOptions.voteId = this.nVoteId
        updateOptions.totalNum = addnum
        // 多选
      } else {
        this.aMultipleChoice = []
        // Object.keys(this.oMultipleNow).forEach((index) => {
        //   if (this.oMultipleNow[index] === true) {
        //     this.aMultipleChoice.push(index)
        //   }
        // })
        this.oMultipleNow.forEach((item, index) => {
          if (item === true) {
            this.aMultipleChoice.push(index)
          }
        })
        if (this.aMultipleChoice.length === 0) {
          this.$message.warning('选择不能为空')
          return
        }
        this.aMultipleChoice.forEach((item, key) => {
          let num = this.oVoteChoice.aVoteChoice[item].votenum
          num++
          addnum++
          this.oVoteChoice.aVoteChoice[item].votenum = num
        })
        updateOptions.options = JSON.stringify(this.oVoteChoice.aVoteChoice)
        updateOptions.voteId = this.nVoteId
        updateOptions.totalNum = addnum
      }
      console.log(updateOptions)
      this.$axios.post('/update', qs.stringify(updateOptions))
        .then((result) => {
          this.bIsVoted = true
          this.visiableVoted = true
          this.getdata()
        })
    },
    fVotedOk () {
      this.visiableVoted = false
    },
    // 显示更多实名名单
    fShowMoreChoicer (val) {
      this.now = val
      this.visiable = true
    },
    handleOk () {
      this.now = 10000
      this.visiable = false
    }
  }
}
</script>

<style>
/* 模态框 */
.votesuccess-modal .ant-modal-body{ text-align: center;}
.choice-modal .ant-modal-body{
  height: 45vh; overflow: scroll;
  display: flex; flex-direction: column; justify-content: space-between;
}
.ant-modal-body .ant-row{ padding: 8px 0;}
.votewho-name{ font-size: 1rem; color: #222;}
.votewho-more-footer{
  width: 100%; height: 100%; margin: 0;
  text-align: center; font-size: 1rem; color: #0A6DD9;
}
</style>

<style scoped>
/* 当前选择样式 */
p.ischoose{
  border-left: 8px solid #108EE9;
}
/* 进度条 */
.ant-progress-default{
  position: absolute; bottom: 12px; left: -8px;
  width: calc(100% + 8px);
}

.votepage{
  width: 100%; height: 100vh;
  overflow: scroll; text-align: left;
  background-color: #f0f2f5;
}
.votepage::-webkit-scrollbar{ display: none;}
/* 头部\标题 */
.votepage-head{
  height: 44px; line-height: 44px; padding: 0 4% 0 3.7%; margin-bottom: 2px;
  text-align: center; font-size: 1.5rem; font-weight: bold;
  background-color: #fff;
}
/* .votepage-head::after{ content:''; display: inline-block; vertical-align: middle; height: 100%;} */
.votepage-head .anticon{ font-size: 1.25rem;}
.head-left{float: left; margin-top: 11px; color: #108EE9;}
.head-right{float: right; margin-top: 11px; color: #108EE9;}

div.votepage-contenttitle{
  min-height: 110px; margin-top: 2px;
}
.votepage-title img{ width: 20px;}
.votepage-title{
  margin: 0;
  font-size: 1.15rem; color: rgba(0,0,0,0.85); font-weight: bold;
}
p.votepage-type{
  margin: 8px 0 12px 0;
  font-size: 1rem; color:rgb(0, 140, 255);
}
.title-right{
  float: right;
  width: 5.3%; margin-top: 3px;
}
.votepage-descript{
  margin: 0;
  font-size: 1rem;
}
/* 每块元素 */
.votepage-contentp{
  width: 100%;
  padding: 20px 4%; margin: 4px 0;
  border-left: 8px solid #fff;
  background-color: #fff;
  font-size: 1rem; color: #000;
  position: relative;
}
/* 实名 */
.votepage-votewho{
  margin: 0 0 0 4%;
}
.votewho-single{
  margin-right: 2.1%;
}
/* 投票信息 */
p.votepage-img{ padding: 8px 15px; min-height: 90px;}
.choice-img{ width: 79px; height: 79px;}
.choice-content{
  display: inline-block;
  width: 40%; position: absolute; top: 8px; left: 27.7%;
  font-size: 1rem;
}
.votepage-votenum{
  float: right;
  width: 32%; margin-right: 0;
  font-size: 0.9rem; text-align: right;
}
.votepage-img .votepage-votenum{ padding: 32px 0;}
.votepage-votenum span:last-child{
  padding-left: 4%;
}
/* 截止时间\按钮 */
.votepage-time{
  padding: 6px 0 0 4%;
  font-size: 14px; color: #888;
}
.votepage-tovote{
  font-size: 18px;
  padding: 0 2.2%; margin-top: 32px;
}
.tovote-bt{
  height: 47px;
}
</style>



